.header {
  width: 100%;
  background-color: white;  // border: solid;
  // height: 80px;
  height: fit-content;
  box-shadow: 0 0 10px #333;
  z-index: 1;
  display: flex;
  // border: orange solid;
}

.links {
  width: 70%;
  // width: fit-content;
  // max-width: 70%;
  display: flex;
  padding: auto;
  text-align: center;

  margin: 0 20% 0 5%;
}

.sticky{
  position: fixed;
  top: 0;
  width: 100%;
}

.small-logo-with-text{
  height: fit-content;
  display: flex;
  width: 20%;
  min-width: fit-content;
  // border: solid red;
}

.small-logo-container{
  // height: fit-content;
  // display: flex;
  // flex-wrap: wrap;
  // width: 20%;
}

.logo-text{
  width: fit-content;
  height: 2em;
  margin-top: 3em;
  font-size: .8em;
  margin-left: .5em;
  min-width: 9em;
  // border: green solid;
  padding: 0;
}

.small-logo{
  height: 5em;
  z-index: 4;
  margin: .5em;
  // border: solid black;
}

.small-logo:hover{
  cursor: pointer;
}

.web-header{
  display: flex;
  width: 100%;
  height: fit-content;
}

.mobile-header{
  width: 100%;
  // border: solid blue;
  height: fit-content;
  display: flex;
  
}

.mobile-title{
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.menu-container{
  // border: solid green;
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: 0;
}

@media (max-width: 468px) {
  // .links{
  //   display: block;
  // }

  .small-logo{
    height: 5em;
  }
  
}
