$akos-orange: #d4af37;

.menu{
  height: 60vh;
  width: 77%;
  max-width: 20em;
  // border: solid red;
  margin: .5em .5em 4em 4em;
  background-color: white;
  opacity: 95%;
  border: solid  $akos-orange;
  border-radius: 2em;
  border-width: .2em;
  float: right;
}

.menu-link{
  text-align: center;
  // border: green solid;
  display: flex;
  width: 100%;
  margin: 0 auto;
  font-size: 2em;
  padding: 0;
}

.menu-link:hover{
  color: $akos-orange;
  // text-shadow: -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black, 1px 1px 0 black;
  cursor: pointer;
}


.divider{
  border-top: solid black;
  width: 50%;
  height: .11em;
  margin: 0 auto;
}